      //               res.data.data.tags[res.data.data.tags.length - 1].value;
      //           }
      //           this.worldData3 = res.data.data.tags;
      //         }
      //         this.drawWorld(this.worldData1, this.worldData2, this.worldData3);
      //         this.drawLine(
      //           this.xdatas1,
      //           this.ydatas1,
      //           this.xdatas2,
      //           this.ydatas2,
      //           this.xdatas3,
      //           this.ydatas3,
      //           this.max1,
      //           this.max2,
      //           this.max3
      //         );
      //         this.$message.success("!");
      //         this.loading = false;
      //       } else {
      //         this.$message.error(res.data.msg);
      //       }
      //     });
      // }
    },
    selectdata() {
      this.xdatas1 = [];
      this.ydatas1 = [];
      this.max1 = "";
      this.worldData1 = [];
      this.xdatas2 = [];
      this.ydatas2 = [];
      this.max2 = "";
      this.worldData2 = [];
      this.xdatas3 = [];
      this.ydatas3 = [];
      this.max3 = "";
      this.worldData3 = [];
      if (this.notes != "" && this.product == "") {
        this.$message.error("!");
        return false;
      } else {
        this.loading = true;
        this.$axios
          .get(window.g.apiUrl + "/xhs/getWordsStatistics", {
            params: {
              noteUrlRecommendId: null,
              twoClassify: this.product
            }
          })
          .then(res => {
            if (res.data.code == 200) {
              if (res.data.data.titles.length != 0) {
                for (var i = 0; i < res.data.data.titles.length; i++) {
                  this.xdatas1.push(res.data.data.titles[i].value);
                  this.ydatas1.push(res.data.data.titles[i].name);
                  res.data.data.titles[
                    i
                  ].textStyle = this.createRandomItemStyle();
                  this.max1 =
                    res.data.data.titles[res.data.data.titles.length - 1].value;
                }

                this.worldData1 = res.data.data.titles;
              }
              if (res.data.data.contents.length != 0) {
                for (var j = 0; j < res.data.data.contents.length; j++) {
                  this.xdatas2.push(res.data.data.contents[j].value);
                  this.ydatas2.push(res.data.data.contents[j].name);
                  res.data.data.contents[
                    j
                  ].textStyle = this.createRandomItemStyle();
                  this.max2 =
                    res.data.data.contents[
                      res.data.data.contents.length - 1
                    ].value;
                }
                this.worldData2 = res.data.data.contents;
              }
              if (res.data.data.tags.length != 0) {
                for (var k = 0; k < res.data.data.tags.length; k++) {
                  this.xdatas3.push(res.data.data.tags[k].value);
                  this.ydatas3.push(res.data.data.tags[k].name);
                  res.data.data.tags[
                    k
                  ].textStyle = this.createRandomItemStyle();
                  this.max3 =
                    res.data.data.tags[res.data.data.tags.length - 1].value;
                }
                this.worldData3 = res.data.data.tags;
              }
              this.drawWorld(this.worldData1, this.worldData2, this.worldData3);
              this.drawLine(
                this.xdatas1,
                this.ydatas1,
                this.xdatas2,
                this.ydatas2,
                this.xdatas3,
                this.ydatas3,
                this.max1,
                this.max2,
                this.max3
              );
              this.$message.success("!");
              this.loading = false;
            } else {
              this.$message.error(res.data.msg);
            }
          });
      }
    },
    addopen() {
      this.$router.push({
        path: "/redbookform" //
      });
    },
    callback() {
      this.$router.push({
        path: "/redbooktable" //
      });
    }
  }
};
</script>
<style scoped>
.row {
  margin: 0px;
}
.selectheader {
  display: inline-block;
  margin: 10px auto;
}
/* .__panel {
  margin-bottom: -17px !important;
} */
</style>




