      >
        <div class="well animate-bounce-up animate-bounce-down">
          <div style="height:500px;">
            <h4>{{item.title}}</h4>
            <p class="baseimglight hoverflow">
              <img :src="item.videoPic" alt="" style="width:100%;height:320px;">
            </p>
            <p>
              <a :href="item.noteLink" target="_blank"></a>
