              <span class="glyphicon glyphicon-heart">{{item.likeCount}}</span>
              <span class="glyphicon glyphicon-user">{{item.commentCount}}</span>
            </p>
            <p>{{item.tagStr}}</p>
            <p>:{{item.publishDate}}</p>
            <p v-if="item.cooperationStr!=null">{{item.cooperationStr}}</p>
          </div>
        </div>
      </div>
    </div>

